import { BridgeComponent } from "@hotwired/hotwire-native-bridge";

class HistorySyncComponentController extends BridgeComponent {
    static component = "historysync";

    connect() {
        super.connect();

        this.element.addEventListener("turbo:next-history-sync", this.sendHistoryTemp.bind(this));
    }

    disconnect() {
        this.element.removeEventListener("turbo:next-history-sync", this.sendHistoryTemp.bind(this));
    }

    sendHistoryTemp(event) {
        const url = event.detail;
        if(url) {
            // Send URL to native app
            this.send("history", { location: url.toString() }, (response) => {
            });
        }

    }
}

export default HistorySyncComponentController;

